.trip {
  margin: 4rem 6rem;
}

.trip h1 {
  font-size: 3rem;
}

.Tripcard {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.t-card {
  width: 32%;
  text-align: start;
  box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
  border-radius: 7px;
  cursor: pointer;
}

.t-image {
  height: 200px;
  overflow: hidden;
}
.t-image img {
  width: 100%;
  height: 100%;
  transition: 0.3s ease-in-out;
}
.t-image:hover img {
  transform: scale(1.3);
}

.t-card h4 {
  font-size: 1.3rem;
  padding: 1rem 0.4rem 0.2rem 0.4rem;
}

.t-card p {
  padding: 1rem 0.4rem 0.2rem 0.4rem;
}

@media screen and (max-width: 850px) {
  .trip {
    margin: 4rem 2rem;
  }
  .Tripcard {
    flex-direction: column;
  }
  .t-card {
    width: 100%;
    margin-bottom: 1.5rem;
  }
}
